import React from "react"
import { ProjectData } from "../../data/Projects";
import { ResumeData } from "../../data/ResumeData"
import './resume.css';

export default class ResumeLayout extends React.Component<{}, {}> {
	public render() {
		return(
			<div className="page">
				<div className="subpage">
					<div id="nameheader">Bryce Egley</div>
					<div id="subHeader">
						<div>Software Developer</div>
						<div className="headerDivider"><b>|</b></div>
						{/*<div>Scottsdale, AZ</div>
						<div className="headerDivider"><b>|</b></div>*/}
						<div>bryce.a.egley@gmail.com</div>
						<div className="headerDivider"><b>|</b></div>
						<div><a href="https://github.com/Mistweaver">https://github.com/Mistweaver</a></div>
						<div className="headerDivider"><b>|</b></div>
						<div><a href="http://bryceegley.com">www.bryceegley.com</a></div>
					</div>
					<div className="block-header">Experience</div>
					{
						ResumeData.map(item => (
							<div className="resume-block" key={item.id}>
								<div className="dataRow1">
									<div className="company">{item.company}</div>
									<div className="when">{item.when}</div>
								</div>
								{
									item.role !== "" && item.where !== "" &&
									<div className="dataRow2">
										<div><i>{item.role}</i></div>
										<div>{item.where}</div>
									</div>
								}
								
								<ul>
									{
										item.details.map(detail => (
											<li key={detail}>{detail}</li>
										))
									}
								</ul>
							</div>
						))
					}
					<div className="block-header">Projects</div>
					{
						ProjectData.map(item => (
							<div key={item.id} className="resume-block">
								<div className="dataRow1">
									<div className="company">{item.company}</div>
									<div className="when">{item.when}</div>
								</div>
								{
									item.role !== "" && item.where !== "" &&
									<div className="dataRow2">
										<div><i>{item.role}</i></div>
										<div>{item.where}</div>
									</div>
								}
								
								{
									item.details.map(detail => (
										<div key={detail} style={{fontSize: '10pt', paddingLeft: 15}}>{detail}</div>
									))
								}
							</div>
						))
					}
					<div className="block-header" style={{marginTop: 7}}>Education: Aeronautical and Aerospace Engineering, Purdue University</div>
					<ul>
						<li>Focus in aeronautical engineering with specialization in dynamics and control systems.</li>
						<li>Worked with MATLAB, CAD software, and printed thermoplastic fabrication.</li>
						<li>Experience with Agile development, Jira, and source control (Git) for project collaboration and management.</li>
						<li>Played collegiate and semi-professional rugby.</li>
						<li>Worked as a contributing writer and columnist for several websites and newspapers.</li>
					</ul>
					{/*
					<div className="block-header" style={{marginTop: 7}}>Miscellaneous Skills and Interests</div>
					<ul>
						<li>Experience with Agile development, Jira, and source control (Git) for project collaboration and management.</li>
						<li>I am an avid aquarist and took courses in college covering aquaponics, biology, and marine biology.</li>
						<li>Highest altitude I have climbed is 14,439ft and have visited over two dozen national parks.</li>
					</ul>
					*/}
				</div>
			</div>
		)
	}
}